.radio {
  display: flex;
  position: relative;
  margin-top: 20px;
}
.radio__checker {
  display: block;
  position: relative;
  min-width: 24px;
  width: 24px;
  height: 24px;
  margin-right: 10px;
  border: 2px solid #BDBDBD;
  border-radius: 50%;
  cursor: pointer;
}
.radio__checker::before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: transparent;
}
.radio__input[type="radio"]:checked + .radio__checker {
  border-color: #7859CF;
}
.radio__input[type="radio"]:checked + .radio__checker::before {
  background-color: #7859CF;
}
.radio__label {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
.radio__label--disabled {
  color: #E2E2E2;
}
.radio__input[type="radio"]:hover + .radio__checker,
.radio__input[type="radio"]:focus + .radio__checker {
  border-color: #9070EC;
}
.radio__input[type="radio"]:hover + .radio__checker::before,
.radio__input[type="radio"]:focus + .radio__checker::before {
  background-color: rgba(120, 89, 207, 0.25);
}
.radio__input:disabled + .radio__checker {
  border-color: #E2E2E2;
}
.radio__input[type="radio"]:checked:disabled + .radio__checker {
  border-color: #E2E2E2;
}
.radio__input[type="radio"]:checked:disabled + .radio__checker::before {
  background-color: #E2E2E2;
}
.checkbox {
  display: flex;
  margin-top: 20px;
}
.checkbox__checker {
  display: block;
  position: relative;
  min-width: 42px;
  width: 42px;
  height: 24px;
  margin-right: 10px;
  border-radius: 12px;
  background-color: #BDBDBD;
  transition: 0.15s;
  cursor: pointer;
  border: 2px solid transparent;
}
.checkbox__checker::before {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  left: 1px;
  top: 0;
  border-radius: 50%;
  background-color: #FFFFFF;
  transition: 0.15s;
}
.checkbox__input[type="checkbox"]:checked + .checkbox__checker {
  background-color: #7859CF;
}
.checkbox__input[type="checkbox"]:checked + .checkbox__checker::before {
  left: 18px;
}
.checkbox__label {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
.checkbox__label--disabled {
  color: #BDBDBD;
}
.checkbox__input[type="checkbox"]:checked:hover + .checkbox__checker,
.checkbox__input:hover + .checkbox__checker,
.checkbox__input[type="checkbox"]:checked:focus + .checkbox__checker,
.checkbox__input:focus + .checkbox__checker {
  border-color: #9070EC;
  background-color: rgba(120, 89, 207, 0.25);
}
.checkbox__input[type="checkbox"]:disabled + .checkbox__checker {
  background-color: #E2E2E2;
}
.checkbox__input[type="checkbox"]:disabled + .checkbox__checker::before {
  background-color: #F8F8F8;
}
.checkbox__input[type="checkbox"]:checked:disabled + .checkbox__checker::before {
  background-color: #BDBDBD;
